import {TemplateHead} from "@/template/TemplateHead";
import {TemplateElement} from "@/template/TemplateElement";
import {User} from "@/api/auth/User";
import {CRUD} from "@/api/CRUD";
import {Category, Request} from "@/api/URI";

export enum TemplateState {
    DRAFT = "Draft",
    PUBLISHED = "Published"
}

export class Template {
    id!: number;
    name!: string;
    description!: string;
    head!: TemplateHead;
    body!: Array<TemplateElement>;
    customStyle!: string;
    owner!: User;
    isPublic!: TemplateState;
    requiredData!: Array<string>;
    inUsed!: number;

    constructor(id: number, name: string, description: string, head: TemplateHead, body: Array<TemplateElement>, customStyle: string, owner: User, isPublic: TemplateState, requiredData: Array<string>, inUsed: number) {
        this.id = id;
        this.name = name;
        this.description = description;
        this.head = head;
        this.body = body;
        this.customStyle = customStyle;
        this.owner = owner;
        this.isPublic = isPublic;
        this.requiredData = requiredData;
        this.inUsed = inUsed;
    }

    async save() {
        localStorage.setItem("loadedTemplate", JSON.stringify(this));
        console.log("saved!");
        const crud = new CRUD(Category.TEMPLATE);
        crud.setRequest(Request.UPDATE);
        crud.setAttachData(this);
        await crud.shoot({});
        crud.log();
    }

    clearBody() {
        this.body = [];
    }

    clone() {
        //
    }

}