import Vue from 'vue'
import VueRouter, {RouteConfig} from 'vue-router'
import {OMS, WebPart} from "@/OMS";
import {FancyMessage} from "@/FancyMessage";
import {User} from "@/api/auth/User";
import {UserRole} from "@/api/auth/UserRole";
import {Auth} from "@/api/auth/Auth";

Vue.use(VueRouter)

const checkPermission: (pageRole: UserRole) => (boolean) = (pageRole: UserRole) => {

  const user: User | null = Auth.getUser();

  /*
  if(!user) {
    setTimeout(() => {
      FancyMessage.sendToast(` `, "is-danger");
      OMS.getInstance().$router.push({name: "Auth"}).then();
    }, 0);
    return false;
  }

  //   
  if(user.role != pageRole) {
    setTimeout(() => {
      FancyMessage.sendToast(` ${user.role}`, "is-warning");
      OMS.getInstance().$router.push({name: "Auth"});
      OMS.redirectUser(user);
    }, 0)
  }*/

  return true;
}

const routes: Array<RouteConfig> = [
  {
    path: '*',
    name: '404',
    redirect: {name: 'Redirect'}
  },
  {
    path: '/redirect',
    name: 'Redirect',
    component: () => import('../views/RedirectView.vue')
  },
  {
    path: '/test',
    name: 'Test',
    component: () => import('../views/TestView.vue')
  },
  {
    path: '/auth',
    name: 'Auth',
    component: () => import('../views/AuthView.vue'),
    beforeEnter: (to, from, next) => {
      // Redirect if user is authenticated.
      OMS.setTabIcon(WebPart.AUTHENTICATION);
      next();
    },
  },
  {
    path: '/auth/reset',
    name: 'Auth@reset',
    component: () => import('../views/auth/Reset.vue'),
    beforeEnter: (to, from, next) => {
      // Redirect if user is authenticated.
      OMS.setTabIcon(WebPart.AUTHENTICATION);
      next();
    },
  },
  {
    path: '/builder',
    name: 'Builder',
    redirect: {name: 'Builder@start'},
    component: () => import('../views/BuilderView.vue'),
    beforeEnter: (to, from, next) => {
      //Check Permission (OMS Section)
      if(checkPermission(UserRole.DESIGNER)) {
        OMS.setTabIcon(WebPart.BUILDER);
        next();
      }
    },
    children: [
      {
        path: 'start',
        name: 'Builder@start',
        component: () => import('../views/builder/ProjectSelection.vue')
      },
      {
        path: 'design',
        name: 'Builder@index',
        component: () => import('../views/builder/Design.vue'),
      },
      {
        path: 'settings',
        name: 'Settings',
        component: () => import('../views/builder/SettingsLayout.vue'),
        children: [
          {
            path: 'template',
            name: 'Settings@template',
            component: () => import('../views/builder/settings/TemplateSettings.vue')
          },
          {
            path: 'media',
            name: 'Settings@media',
            component: () => import('../views/builder/settings/MediaManager.vue')
          }
        ]
      },
    ]
  },
    /*
    * Route   Administrator
    */
  {
    path: '/console',
    name: 'Console',
    redirect: {name: 'Console@index'},
    component: () => import('../views/ConsoleView.vue'),
    beforeEnter: (to, from, next) => {
      if(checkPermission(UserRole.ADMINISTRATOR)) {
        OMS.setTabIcon(WebPart.CONSOLE);
        next();
      }
    },
    children: [
      {
        path: 'manage',
        name: 'Console@layout',
        component: () => import('../views/console/ConsoleLayout.vue'),
        children: [
          {
            path: '',
            name: 'Console@index',
            component: () => import('../views/console/ManageUser.vue')
          },
          {
            path: 'user',
            name: 'Console@user',
            component: () => import('../views/console/ManageUser.vue')
          },
          {
            path: 'template',
            name: 'Console@template',
            component: () => import('../views/console/ManageTemplate.vue')
          },
          {
            path: 'standardElement',
            name: 'Console@standardelement',
            component: () => import('../views/console/ManageStandardElement.vue')
          },
          {
            path: 'customElement',
            name: 'Console@customelement',
            //component: () => import('../views/console/ManageCustomElement.vue')
          },
          {
            path: 'media',
            name: 'Console@media',
            //component: () => import('../views/console/ManageMedia.vue')
          }
        ]
      }
    ]
  },
  {
    path: '/generator',
    name: 'Generator',
    component: () => import('../views/GeneratorView.vue'),
    beforeEnter: (to, from, next) => {
      checkPermission(UserRole.MARKETER);
      OMS.setTabIcon(WebPart.GENERATOR);
      next();
    },
    children: [
      {
        path: '#',
        name: 'Generator@layout',
        redirect: { name: 'Generator@start' },
        component: () => import('../views/generator/GeneratorLayout.vue'),
        children: [
          {
            path: 'start',
            name: 'Generator@start',
            alias: '/generator',
            component: () => import('../views/generator/Generator.vue')
          }
        ],
      },
    ]
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
