import {User} from "@/api/auth/User";
import axios from "axios";
import {OMS} from "@/OMS";
import {FancyMessage} from "@/FancyMessage";
import {Category, Request, URI} from "@/api/URI";
import {CRUD} from "@/api/CRUD";
import {UserAuthVault} from "@/api/storage/UserAuthVault";

export class Auth {

  private static userVault = new UserAuthVault();

  public static async login(username: string, password: string): Promise<Auth> {
    const crud = new CRUD(Category.USER);
    crud.setRequest(Request.LOGIN);
    await crud.shoot({username, password});
    crud.log();

    if(crud.getResponseInfo().apistatus === 'OK') {
      console.log(crud.getResult().auth_user);
      this.userVault.setUser(crud.getResult().auth_user);

      await OMS.getInstance().$router.push({name: "Redirect"}).then(() => {
        FancyMessage.sendToast(" " + crud.getResult().auth_user.username, "is-success");
      });
    }
    return this;
  }

  public static async resetPassword(email: string): Promise<Auth> {
    return this;
  }

  public static getUser(): User | null {
    return this.userVault.getUser();
  }

  public static isLogged(): boolean {
    return this.userVault.getUser() instanceof User;
  }

  public static logout(): Auth {
    if(Auth.isLogged()) {
      this.userVault.clear();
      OMS.getInstance().$router.push({ name: "Auth" }).then();
    }

    FancyMessage.sendToast("", "is-success");
    return this;
  }


}