export class ErrorHistory {
  id!: number;
  errorInfo!: object;
  createdDate!: string;

  constructor(id: number, errorInfo: object, createdDate: string) {
    this.id = id;
    this.errorInfo = errorInfo;
    this.createdDate = createdDate;
  }
}

export class OfflineErrorHistory {

  private history: Array<ErrorHistory> = JSON.parse(localStorage.getItem("offline_history") as string) || [];

  constructor() {
    if(localStorage.getItem("offline_history") === null) {
      localStorage.setItem("offline_history", JSON.stringify(this.history));
    }
  }

  getHistory(): Array<ErrorHistory> {
    return this.history;
  }

  add(code: number, message: string) {
    this.history.push(new ErrorHistory(0, {error_code: code, error_message: message}, ""));
    console.log(this.history);
    this.save();
  }

  clear() {
    this.history = [];
    this.save();
  }

  save() {
    localStorage.setItem("offline_history", JSON.stringify(this.history));
  }
}