import {Vue} from 'vue-property-decorator';
import {TemplateHead} from "./template/TemplateHead";
import {BrowserUtils, LogType} from "./BrowserUtils";
import {TemplateElement} from "./template/TemplateElement";
import {StandardElement} from "@/template/StandardElement";
import {CustomElement} from "@/template/CustomElement";
import {Color, Position} from "@/BuefyUtils";
import {Template} from "@/template/Template";
import {User} from "@/api/auth/User";

import { DialogProgrammatic as Dialog } from 'buefy';
import { ToastProgrammatic as Toast } from 'buefy';
import {UserRole} from "@/api/auth/UserRole";
import Router from "@/router";
import {CounterManager} from "@/CounterManager";
import {CRUD} from "@/api/CRUD";
import {Category, Request} from "@/api/URI";
import {TemplateStorage} from "@/api/TemplateStorage";
import {UserAuthVault} from "@/api/storage/UserAuthVault";

export enum DebugActionType {
  TRUE ,
  FALSE,
  TOGGLE
}

export enum WebPart {
  BUILDER = '/icon/builder.ico',
  GENERATOR = '/icon/generator.ico',
  CONSOLE = '/icon/manage.ico',
  AUTHENTICATION = '/icon/auth.ico'
}

export class OMS {

  private static _builderInstance: Vue;

  public static getInstance(): Vue {
    if(this._builderInstance === null) {
      BrowserUtils.debugMessage(LogType.WARNING, "[BuilderUtils] Instance is not register! Now return a new one.");
      return new Vue;
    }
    return this._builderInstance;
  }

  public static registerInstance(value: Vue): Vue {
    try {
      this._builderInstance = value;
      return this._builderInstance;
    } catch (e) { BrowserUtils.log(LogType.ERROR, "OMS Instance ") }
    return this._builderInstance;
  }

  public static setLoggedUser(user: User): void {
    this.getInstance().$store.dispatch("setLoggedUser", user).then();
    if(BrowserUtils.debugMode) { console.log(`[Authentication] Logged in as ${user.username}: ${user.email}. @${user.role}`) }
  }

  public static setTemplate(template: Template): void {
    this.getInstance().$store.dispatch("setTemplate", template).then();
    if(BrowserUtils.debugMode) { console.log("[Set] 'Template' successfully assigned!") }
  }

  public static setHead(head: TemplateHead): void {
    this.getInstance().$store.dispatch("setHead", head).then();
    if(BrowserUtils.debugMode) { console.log("[Set] 'Head' successfully assigned!") }
  }

  public static setBody(body: Array<TemplateElement>): void {
    this.getInstance().$store.dispatch("setBody", body).then();
    if(BrowserUtils.debugMode) { console.log("[Set] 'Body' successfully assigned!") }
  }

  public static setHoveredElement(element: TemplateElement): void {
    this.getInstance().$store.dispatch("updateHoveredStandardElement", element).then();
    if(BrowserUtils.debugMode) { console.log("[Set] 'updateHoveredStandardElement' successfully assigned!") }
  }

  public static setSelectedElement(element: TemplateElement): void {
    this.getInstance().$store.dispatch("updateSelectedElement", element).then(() => {
      if(BrowserUtils.debugMode) { console.log(`[Set] 'updateSelected' New selected info successfully assigned : As @${element.tag}!`) }
    });
  }

  public static showSnackbar(message: string, buttonText: string, color: Color, position: Position, keepShow: boolean): OMS {
     this.getInstance().$buefy.snackbar.open({
      message,
      type: color,
      position,
      actionText: buttonText,
      indefinite: keepShow,
    })
    return this;
  }

  public static showToast(message: string, color: Color, position: Position, duration: number): OMS {
    this.getInstance().$buefy.toast.open({
      duration,
      message,
      position,
      type: color
    })
    return this;
  }

  public static setDebugMode(mode: DebugActionType): void {
    switch (mode) {
      case DebugActionType.TRUE: {
        this.getInstance().$store.dispatch("setDebugMode", true).then();
        break;
      }
      case DebugActionType.FALSE: {
        this.getInstance().$store.dispatch("setDebugMode", false).then();
        break;
      }

      case DebugActionType.TOGGLE: {
        const newValue: boolean = this.isDebugMode();
        this.getInstance().$store.dispatch("setDebugMode", !newValue).then(() => {
          BrowserUtils.log(LogType.WARNING, " Debug Mode  " + this.isDebugMode());
        });
        break;
      }
      default: {
        BrowserUtils.log(LogType.WARNING, " Debug Mode  ")
        break;
      }
    }
  }

  public static setCustomStyleSource(href: string): void {
    const styleSource = window.document.getElementById('theme_css')!;
    if (styleSource === null) {
      OMS.showToast('', Color.WARNING, Position.TOP, 3000);
      return;
    } else {
      // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
      // @ts-ignore
      styleSource.href = href;
    }
  }

  public static setTabIcon(icon: WebPart = WebPart.BUILDER): void {
    const iconSource = window.document.getElementById('theme_icon')!;
    if (iconSource === null) {
      OMS.showToast('', Color.DANGER, Position.TOP, 3000);
      return;
    } else {
      // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
      // @ts-ignore
      iconSource.href = icon;
    }
  }

  public static showErrorMessage(text: { message: string, title: string, redirectToRouteName: string }) {
    Dialog.alert({
      type: 'is-danger',
      title: text.title || "!",
      message: text.message || "!",
      confirmText: '',
      canCancel: false,
      onConfirm: () => { OMS.getInstance().$router.push({name: text.redirectToRouteName}).then() }
    })
  }

  public static showAlert(title: string, message: string, type: string, hasIcon: boolean) {
    Dialog.alert({
      title,
      message,
      type,
      hasIcon,
      icon: 'times-circle',
      iconPack: 'fa',
      ariaRole: 'alertdialog',
      ariaModal: true
    })
  }

  public static parseTemplate(template: Template): Template | null {
    if(!template) return null;
    return new Template(
        template.id,
        template.name,
        template.description,
        new TemplateHead(template.head.title, template.head.icon, template.head.meta, template.head.externalLink),
        this.processTemplateElement(template.body),
        template.customStyle,
        template.owner,
        template.isPublic,
        template.requiredData,
        template.inUsed
    );
  }

  public static processStandardElement(elements: Array<StandardElement>): Array<StandardElement> {
    const elementList: Array<StandardElement> = [];
    elements.forEach((value) => {
      elementList.push(
          new StandardElement(0, value.name, value.description, value.icon, value.tag, value.props, value.inner, value.parentTags, value.childTags).setChildren(this.processTemplateElement(value.children)))
    })
    return elementList;
  }

  public static processCustomElement(elements: Array<CustomElement>): Array<CustomElement> {
    const elementList: Array<CustomElement> = [];
    elements.forEach((value) => {
      elementList.push(
          new CustomElement(0, value.name, value.description, value.owner, value.tag, value.inner, value.props, value.parentTags, value.childTags).setChildren(this.processTemplateElement(value.children)))
    })
    return elementList;
  }

  public static processTemplateElement(elements: Array<TemplateElement>): Array<TemplateElement> {
    const elementList: Array<TemplateElement> = [];
    elements.forEach((value, index) => {
      elementList.push(new TemplateElement(value.tag, value.props, value.inner, value.parentTags, value.childTags).setChildren(this.processTemplateElement(value.children)))
    })
    return elementList;
  }

  public static async saveTemplate() {
    localStorage.setItem("loadedTemplate", JSON.stringify(this.getInstance().$store.getters.template));
    const crud = new CRUD(Category.TEMPLATE);
    crud.setRequest(Request.UPDATE);
    crud.setAttachData(TemplateStorage.getTemplate());
    await crud.shoot({});
    crud.log();
  }

  public static saveTemplateInstantly(): void {
    CounterManager.setTime(1);
    CounterManager.start();
  }

  public static redirectUser(user: User): void {
    switch (user.role) {
      case UserRole.ADMINISTRATOR:
        Router.push({name: "Console@index"}).then();
        break;
      case UserRole.MARKETER:
        Router.push({name: "Generator@start"}).then();
        break;
      case UserRole.DESIGNER:
        Router.push({name: "Builder@index"}).then();
        break;
      default:
        Router.push({name: "Auth"}).then();
        break;
    }
  }

  public static pendingSave(): void {
    if(CounterManager.getTime() === 0) {
      CounterManager.setTime(7);
      CounterManager.start();
      return;
    }
    CounterManager.setTime(7);
  }

  public static cleanUp(): void {
    this.getInstance().$store.state.selectedElement = [];
  }

  public static save(): OMS {
    this.getTemplate().save();
    return this;
  }

  public static setUser(user: User) {
    localStorage.setItem("loggeduser", JSON.stringify(user));
  }

  public static getBrowser(): BrowserUtils {
    return new BrowserUtils;
  }

  public static getTemplate(): Template {
    return this.getInstance().$store.getters.template;
  }

  public static getHead(): TemplateHead {
    return this.getInstance().$store.getters.head;
  }

  public static getBody(): Array<TemplateElement> {
    return this.getInstance().$store.getters.body;
  }

  public static getUser() {
    return localStorage.getItem("loggeduser");
  }

  public static getLoggedUser(): User {
    const user = new UserAuthVault();
    return user.getUser() as User;
  }

  public static getSelectedElement(): TemplateElement {
    return this.getInstance().$store.getters.selectedElement;
  }

  public static getHoveredElement(): TemplateElement {
    return this.getInstance().$store.getters.hoveredStandardElement;
  }

  public static getStandardElement(): Array<StandardElement> {
    return this.getInstance().$store.getters.standardElement;
  }

  public static getCustomElement(): Array<CustomElement> {
    return this.getInstance().$store.getters.customElement;
  }

  public static isDebugMode(): boolean {
    return this.getInstance().$store.getters.isDebugMode;
  }
}