import Vue from "vue";
import {OMS} from "@/OMS";

export class CounterManager {
  private static instance: Vue;

  public static registerInstance(instance: Vue) {
    this.instance = instance;
  }

  public static start() {
    // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
    // @ts-ignore
    this.instance.$refs.counter.countDownTimer();
  }

  public static resetTimer() {
    // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
    // @ts-ignore
    this.instance.$refs.counter.reset(7);
  }

  public static getTime(): number {
    // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
    // @ts-ignore
    return this.instance.$refs.counter.countDown;
  }

  public static setTime(second: number) {
    // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
    // @ts-ignore
    this.instance.$refs.counter.countDown = second;
  }

  public static onCounting(currentTime: number) {
    console.log("[AutoSave]  " + currentTime + " ");
  }

  public static onOver() {
    console.log("[AutoSave] ...");
    OMS.saveTemplate().then(() => {
      console.log("[AutoSave] !");
    });
  }

}