import {Template} from "@/template/Template";
import {TemplateHead} from "@/template/TemplateHead";
import {StandardElement} from "@/template/StandardElement";
import {CustomElement} from "@/template/CustomElement";
import {TemplateElement} from "@/template/TemplateElement";
import {User} from "@/api/auth/User";
import {Media, MediaType} from "@/template/Media";

export class TypeParser {
  public static parseUser(user: User): User {
    return new User(
        user.id,
        user.username,
        user.email,
        user.firstName,
        user.lastName,
        user.profileImg,
        user.role,
        user.token
    );
  }

  public static parseUserList(users: Array<User>): Array<User> {
    const userList: Array<User> = [];
    users.forEach((value) => {
      userList.push(this.parseUser(value));
    })
    return userList;
  }

  public static parseTemplate(template: Template): Template {
    return new Template(
        template.id,
        template.name,
        template.description,
        new TemplateHead(template.head.title, template.head.icon, template.head.meta, template.head.externalLink),
        this.processTemplateElementList(template.body),
        template.customStyle,
        this.parseUser(template.owner),
        template.isPublic,
        template.requiredData,
        template.inUsed
    );
  }

  public static parseTemplateList(templates: Array<Template>): Array<Template> {
    const templateList: Array<Template> = [];
    templates.forEach((value) => {
      templateList.push(this.parseTemplate(value));
    });
    return templateList;
  }

  public static parseStandardElement(element: StandardElement): StandardElement {
    return new StandardElement(element.id, element.name, element.description, element.icon, element.tag, element.props, element.inner, element.parentTags, element.childTags)
        .setChildren(this.processTemplateElementList(element.children))
        .setLock(element.isLock)
        .setTypeTags(element.typeTags);
  }

  public static parseStandardElementList(elements: Array<StandardElement>): Array<StandardElement> {
    const elementList: Array<StandardElement> = [];
    elements.forEach((value) => {
      elementList.push(this.parseStandardElement(value));
    });
    return elementList;
  }

  public static parseCustomElement(element: CustomElement): CustomElement {
    return new CustomElement(
        element.id,
        element.name,
        element.description,
        this.parseUser(element.owner),
        element.tag,
        element.inner,
        element.props,
        element.parentTags,
        element.childTags)
        .setChildren(this.processTemplateElementList(element.children))
        .setLock(element.isLock);
    // TYPE TAG FOR FIST LAYER NOT DEFINE YET
  }

  public static parseCustomElementList(elements: Array<CustomElement>): Array<CustomElement> {
    const elementList: Array<CustomElement> = [];
    elements.forEach((value) => {
      elementList.push(this.parseCustomElement(value));
    })
    return elementList;
  }

  public static processTemplateElement(element: TemplateElement): TemplateElement {
    return new TemplateElement(element.tag, element.props, element.inner, element.parentTags, element.childTags)
        .setChildren(this.processTemplateElementList(element.children))
        .setLock(element.isLock)
        .setTypeTags(element.typeTags);
  }

  public static processTemplateElementList(elements: Array<TemplateElement>): Array<TemplateElement> {
    const elementList: Array<TemplateElement> = [];
    elements.forEach((value, index) => {
      elementList.push(this.processTemplateElement(value));
    })
    return elementList;
  }

  public static parseMedia(media: Media): Media {
    return new Media(media.id, media.name, media.description, media.type as MediaType, media.directLink, media.createdDate);
  }

  public static parseMediaList(media: Array<Media>): Array<Media> {
    const mediaList: Array<Media> = [];
    media.forEach((value) => {
      mediaList.push(this.parseMedia(value));
    })
    return mediaList;
  }

}