import {UserRole} from "@/api/auth/UserRole";

export class User {
  id: number;
  username: string;
  email: string;
  firstName: string;
  lastName: string;
  profileImg: string;
  role: UserRole;
  token: string;

  constructor(id: number, username: string, email: string, firstName: string, lastName: string, profileImg: string, role: UserRole, token: string) {
    this.id = id;
    this.username = username;
    this.email = email;
    this.firstName = firstName;
    this.lastName = lastName;
    this.profileImg = profileImg;
    this.role = role;
    this.token = token;
  }

  get isAdmin(): boolean {
    if(this.role === UserRole.ADMINISTRATOR) { return true }
    return false;
  }

  get isMarketer(): boolean {
    if(this.role === UserRole.MARKETER) { return true }
    return false;
  }

  get isDesigner(): boolean {
    if(this.role === UserRole.DESIGNER) { return true }
    return false;
  }

}