import {TemplateElement} from "@/template/TemplateElement";
import {OMS} from "@/OMS";
import VueRouter, {Route} from "vue-router";

export enum LogType {
  DEFAULT = "Default",
  INFO = "Info",
  WARNING = "Warning",
  ERROR = "Error"
}

export class BrowserUtils {

  public static get debugMode() { return OMS.getInstance().$store.getters.isDebugMode }

  public static debugMessage(type: LogType, msg: string): void {
    if(BrowserUtils.debugMode) { BrowserUtils.log(type, msg); }
  }

  public static log(type: LogType, msg: string): void {
    switch (type) {
      case LogType.DEFAULT:
        console.log(`%c[${type}] ${msg}`, '#00bfff');
        break;
      case LogType.INFO:
        console.info(`[${type}] ${msg}`);
        break;
      case LogType.WARNING:
        console.warn(`[${type}] ${msg}`);
        break;
      case LogType.ERROR:
        console.error(`[${type}] ${msg}`);
        break;
      default:
        console.log(`[${type}] ${msg}`);
        break;
    }
  }

  public static welcome(): void {
    console.log(
        "+------------------------------------+ \n" +
        "|        SAMT Template OMS       | \n" +
        "| Developed by gamelovemiku for SAMT | \n" +
        "|         v4.2a development          | \n" +
        "+------------------------------------+"
    );
  }

  public static setPageTitle(title: string): void {
    document.title = title;
  }

  public static getRoute(): Route {
    return OMS.getInstance().$route
  }

  public static getRouter(): VueRouter {
    return OMS.getInstance().$router
  }

  public static randomHEXString(): string {
    return `#${Math.floor(Math.random()*16777215).toString(16)}`;
  }

  public static randomString(length: number): string {
    let result = '';
    const characters = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
    const charactersLength = characters.length;
    for (let i = 0; i < length; i++ ) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
  }

}