









































import {Component, Vue} from "vue-property-decorator";
import {CounterManager} from "@/CounterManager";

@Component({
  name: "CounterBox",
  components: {}
})

export default class CounterBox extends Vue {
  //Map Value to VueX
  countDown = 0;

  get stamp() {
    const date = new Date().toLocaleString();
    return date;
  }

  countDownTimer() {
    if(this.countDown == 0) {
      this.onCountEnd();
      return;
    }

    if(this.countDown > 0) {
      setTimeout(() => {
        if(this.countDown == 0) { return }

        this.onCounting();
        this.countDown -= 1
        this.countDownTimer();
      }, 1000)
    }
  }

  reset(second: number) {
    this.countDown = second;
    this.countDownTimer();
  }

  onCounting() {
    CounterManager.onCounting(this.countDown);
  }

  onCountEnd() {
    console.log('onCountEnd()');
    CounterManager.onOver();
  }

}
