import {AreaTag} from "@/template/area/AreaTag";
import {BrowserUtils, LogType} from "@/BrowserUtils";
import {OMS} from "@/OMS";
import {Category, Request} from "@/api/URI";
import {CRUD} from "@/api/CRUD";
import {TypeTag} from "@/template/area/TypeTag";

export class TemplateElement {
  tag: string;
  props: object;
  inner: string;
  name!: string;
  typeTags: Array<TypeTag> = [];
  parentTags: Array<AreaTag> = [];
  childTags: Array<AreaTag> = [];
  isLock = false;
  children: Array<TemplateElement> = new Array<TemplateElement>();

  constructor(tag: string, props: object, inner: string, parentTags: Array<AreaTag>, childTags: Array<AreaTag>) {
    this.tag = tag;
    this.props = props;
    this.inner = inner;

    this.parentTags = parentTags;
    this.childTags = childTags;
  }

  setTag(tag: string) {
    this.tag = tag;
    return this;
  }

  setLock(lock: boolean) {
    this.isLock = lock;
    return this;
  }

  setProps(prop: object) {
    this.props = prop;
    return this;
  }

  setInner(inner: string) {
    this.inner = inner;
    return this;
  }

  setName(name: string) {
    this.name = name;
    return this;
  }

  get getSign(): string {
    // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
    // @ts-ignore
    return `${this.tag}.${this.props.class}: ${this.inner}`;
  }

  showInfo(): TemplateElement {
    console.log(`  %c${this.tag} (${this.name})`, "color:lightgreen; font-weight: 600;");
    console.log(this);
    return this;
  }

  canPutParent(parentElement: TemplateElement): boolean {
    if(this.childTags.includes(AreaTag.NO_MORE_CHILD)) return false; //Can't Drag into it

    let put = false;

    try {
      parentElement.childTags.forEach((value) => {
        if(OMS.getHoveredElement().parentTags.includes(value)) {
          put = true;
        }
      });
      return put;
    } catch (e) {
      BrowserUtils.debugMessage(LogType.INFO, "[ERROR]  Standard Element  Standard Element")
    }

    try {
      OMS.getHoveredElement().childTags.forEach((value) => {
        if(!this.childTags.includes(value)) {
          put = true;
        }
      });
      return put;
    } catch (e) {
      BrowserUtils.debugMessage(LogType.INFO, "[ERROR]  Standard Element  Standard Element")
    }
    return false;
  }

  clone(): TemplateElement {
    return this.parseElement(this);
  }

  private parseElement(element: TemplateElement): TemplateElement {
    return new TemplateElement(
            JSON.parse(JSON.stringify(element.tag)),
            JSON.parse(JSON.stringify(element.props)),
            JSON.parse(JSON.stringify(element.inner)),
            JSON.parse(JSON.stringify(element.parentTags)),
            JSON.parse(JSON.stringify(element.childTags)))
        .setChildren(this.parseElementList(element.children))
        .setLock(element.isLock)
        .setTypeTags(JSON.parse(JSON.stringify(element.typeTags)));
  }

  private parseElementList(elements: Array<TemplateElement>): Array<TemplateElement> {
    const elementList: Array<TemplateElement> = [];
    elements.forEach((value) => {
      elementList.push(this.parseElement(value));
    });
    return elementList;
  }

  addParentTag(area: AreaTag) {
    if(this.parentTags.includes(area)) {
      BrowserUtils.debugMessage(LogType.WARNING, `[IGNORE] Element  ${this.name}  ${area} `);
      return this;
    }
    this.parentTags.push(area);
    return this;
  }

  setParentTags(tags: Array<AreaTag>) {
    this.parentTags = tags;
    return this;
  }

  removeParentTag(area: AreaTag) {
    if(this.parentTags.includes(area)) {
      this.parentTags.forEach( (item, index) => {
        if(item === area) this.parentTags.splice(index,1);
      });
    }
    return this;
  }

  hasParentTag(tag: AreaTag): boolean {
    return this.parentTags.includes(tag);
  }

  setTypeTags(type: Array<TypeTag>) {
    this.typeTags = type;
    return this;
  }

  removeTypeTag(type: TypeTag) {
    if(this.typeTags.includes(type)) {
      this.typeTags.forEach( (item, index) => {
        if(item === type) this.typeTags.splice(index,1);
      });
    }
    return this;
  }

  hasTypeTag(type: TypeTag): boolean {
    return this.typeTags.includes(type);
  }

  addChildTag(area: AreaTag): TemplateElement {
    if(this.childTags.includes(area)) {
      BrowserUtils.debugMessage(LogType.WARNING, `[IGNORE] Element  ${this.name}  ${area} `);
      return this;
    }
    this.childTags.push(area);
    console.log("PUSHED");
    return this;
  }

  setChildTags(tags: Array<AreaTag>) {
    this.childTags = tags;
    return this;
  }

  removeChildTag(area: AreaTag): TemplateElement {
    if(this.childTags.includes(area)) {
      this.childTags.forEach( (item, index) => {
        if(item === area) this.childTags.splice(index,1);
      });
    }
    return this;
  }

  hasChildTag(tag: AreaTag): boolean {
    return this.childTags.includes(tag);
  }

  addChild(element: TemplateElement): TemplateElement {
    this.children.push(element);
    return this;
  }

  setChildren(elements: Array<TemplateElement>) {
    this.children = elements;
    return this;
  }

  removeChild(element: TemplateElement) {
    if(this.children.includes(element)) {
      this.children.forEach( (item, index) => {
        if(item === element) this.children.splice(index,1);
      });
    }
    return this;
  }
}