



















































































































































































































































































































import {Component, Vue} from "vue-property-decorator";
import {DebugActionType, OMS} from "@/OMS";
import {TemplateElement} from "@/template/TemplateElement";
import {Auth} from "@/api/auth/Auth";
import {User} from "@/api/auth/User";
import {Template} from "@/template/Template";
import CounterBox from "@/components/BuilderUI/navbar/CounterBox.vue";
import {CounterManager} from "@/CounterManager";
import {AreaTag} from "@/template/area/AreaTag";
import {TypeTag} from "@/template/area/TypeTag";

@Component({
  name: "BuilderNavbar",
  components: {CounterBox}
})

export default class BuilderNavbar extends Vue {

  get body() { return OMS.getTemplate() }
  get template(): Template { return this.$store.state.template }
  get loggedUser(): User { return OMS.getLoggedUser() }

  beforeMount() {
    CounterManager.registerInstance(this);
    console.log(`%c[UI] 'Nav' has been spawned!`, 'color: #bada55');
  }

  addNewSection() {
    this.template.body.push(
        new TemplateElement(
            "section",
            {class:"hero is-white is-fullheight"},
            "",
            [],
            [])
            .setTypeTags([TypeTag.SECTIONS])
            .setChildren([
                new TemplateElement(
                    "div",
                    {class:"hero-body"},
                    "",
                    [],
                    [])
                .setChildren([
                  new TemplateElement(
                      "div",
                      {class:"container"},
                      "",
                      [],
                      [])
                    .setChildren([
                  new TemplateElement(
                      "div",
                      {class:"columns"},
                      "",
                      [AreaTag.LAYOUT],
                      [])
                      .setTypeTags([TypeTag.COLUMNS])
                      .setChildren([
                    new TemplateElement(
                        "div",
                        {class:"column is-6"},
                        "",
                        [],
                        [AreaTag.TYPOGRAPHY, AreaTag.BUTTON_GROUP]),
                    new TemplateElement(
                        "div",
                        {class:"column is-6"},
                        "",
                        [],
                        [AreaTag.TYPOGRAPHY, AreaTag.BUTTON_GROUP])
                  ])
                ])
            ])]))
  }

  selectMode(mode: string) {
    let select = TypeTag.NONE;
    switch (mode) {
      case 'TEXT':
        select = TypeTag.TEXT;
        break;
      case 'BUTTON':
        select = TypeTag.BUTTON;
        break;
      case 'MEDIA':
        select = TypeTag.MEDIA;
        break;
      case 'FORM':
        select = TypeTag.FORM;
        break;
      case 'COLUMNS':
        select = TypeTag.COLUMNS;
        break;
      case 'SECTIONS':
        select = TypeTag.SECTIONS;
        break;
      default:
        select = TypeTag.NONE;
          break;
    }
    this.$store.dispatch("setSelectMode", select);
  }

  save() {
    OMS.saveTemplateInstantly();
  }

  toggleDebugMode() {
    OMS.setDebugMode(DebugActionType.TOGGLE);
  }

  changeStyle() {
    OMS.setCustomStyleSource('https://maxcdn.bootstrapcdn.com/bootstrap/4.0.0/css/bootstrap.min.css');
  }

  logout() {
    Auth.logout();
  }
}
