export class ExternalLink {
  rel: string;
  type: string;
  href: string;
  sizes: string;

  constructor(rel: string, type: string, href: string, sizes: string) {
    this.rel = rel;
    this.type = type;
    this.href = href;
    this.sizes = sizes;
  }
}

export class MetaContent {
  name: string;
  content: string;

  constructor(name: string, content: string) {
    this.name = name;
    this.content = content;
  }
}

export class TemplateHead {
  title: string;
  icon: string;
  meta: Array<MetaContent>;
  externalLink: Array<ExternalLink>;

  constructor(title: string, icon: string, meta: Array<MetaContent>, externalLink: Array<ExternalLink>) {
    this.title = title;
    this.icon = icon;
    this.meta = meta;
    this.externalLink = externalLink;
  }
}