import {TypeParser} from "@/api/TypeParser";
import {User} from "@/api/auth/User";

export class UserAuthVault {

  private storageKey: string = "logged_user";
  private defaultValue: any = null;

  private storageData: User | null = JSON.parse(localStorage.getItem(this.storageKey) as string) || this.defaultValue;

  constructor() {
    if(localStorage.getItem(this.storageKey) === null) {
      localStorage.setItem(this.storageKey, JSON.stringify(this.storageData));
    }
  }

  getUser(): User | null {
    if(!this.storageData) return null;
    return TypeParser.parseUser(this.storageData);
  }

  setUser(user: User): void {
    console.log(user);
    this.storageData = user;
    this.save();
  }

  clear() {
    this.storageData = null;
    this.save();
  }

  save() {
    localStorage.setItem(this.storageKey, JSON.stringify(this.storageData));
  }
}