import { ToastProgrammatic as Toast } from 'buefy';

export class FancyMessage {

  public static sendToast(message: string, color = "is-dark"): FancyMessage {
    Toast.open({
      duration: 6000,
      message,
      position: "is-top",
      type: color,
      queue: false
    });
    return this;
  }
}