export enum Request {
  GET = '/get',
  CREATE = '/create',
  UPDATE = '/update',
  DELETE = '/delete',
  LOGIN = '/signin',
  RESET = '/reset',
  GENERATE = '/generate',
}

export enum Category {
  GENERATOR = "/generator",
  TEMPLATE = "/template",
  STANDARD_ELEMENT = "/element/standard",
  CUSTOM_ELEMENT = "/element/custom",
  MEDIA = "/media",
  USER = "/user"
}

export class URI {
  /**
   * Server Destination
   */
  public static readonly LOCAL_PATH: string = "/api/api-test";
  public static readonly LOCAL_PATH_ROOT: string = "/api/api-test/";

  /**
   * Get URI string
   */
  public static getAPIString(requestType: Request, contentType: Category) {
    return `${URI.LOCAL_PATH}${contentType}${requestType}`
  }
}