import {TemplateElement} from "@/template/TemplateElement";
import {AreaTag} from "@/template/area/AreaTag";

export class StandardElement extends TemplateElement {
    id: number;
    name: string;
    description: string;
    icon: string;

    constructor(id: number, name: string, description: string, icon: string, tag: string, props: object, inner: string, parentTags: Array<AreaTag>, childTags: Array<AreaTag>) {
        super(tag, props, inner, parentTags, childTags);
        this.id = id;
        this.name = name;
        this.description = description;
        this.icon = icon;
    }
}