import {Auth} from "@/api/auth/Auth";
import {Category, Request, URI} from "@/api/URI";
import axios from "axios";
import {FancyMessage} from "@/FancyMessage";
import {TypeParser} from "@/api/TypeParser";
import { DialogProgrammatic as Dialog } from 'buefy';
import {OMS} from "@/OMS";
import {OfflineErrorHistory} from "@/api/OfflineErrorHistory";

export class CRUD {
  private readonly category!: Category;
  private request!: Request;
  private attachData!: object;
  private responseInfo!: { apistatus: string, error_code: string, error_message: string, error_message_display: string };
  private result!: any;

  private successAction: void;
  private errorAction: void;

  private state = true;
  private sentData = {};

  private header = {
    'Access-Control-Allow-Origin': '*',
    'Content-Type': 'application/json'
  }

  constructor(category: Category) {
    this.category = category;
  }

  private get authentication() {
    console.log(Auth.getUser())
    return JSON.stringify({token: Auth.getUser()?.token}); }

  public get status(): boolean {
    return this.state;
  }

  private setState(state: boolean): void {
    this.state = state;
  }

  private setResult(result: any) {
    this.result = result;
  }

  public getResult(): any {
    return this.result;
  }

  public getSentDataInfo(): any {
    return this.sentData;
  }

  private setSentDataInfo(data: object) {
    this.sentData = data;
  }

  public setRequest(request: Request) {
    this.request = request;
  }

  public setAttachData(data: any) {
    this.attachData = data;
  }

  private getAttachData(): object {
    return this.attachData;
  }

  public onSuccess(): void {
    return this.successAction;
  }

  public onError(): void {
    return this.errorAction;
  }

  public OnSuccessAction(action: void) {
    this.successAction = action;
  }

  public OnErrorAction(action: void) {
    this.errorAction = action;
  }

  public async shoot(parameter: object): Promise<CRUD> {
    this.setState(true);

    const history = new OfflineErrorHistory();
    history.add(0, "Shoot " + URI.getAPIString(this.request, this.category));

    this.setSentDataInfo({
      ...(this.attachData),
      ...parameter,
      offlineHistory: history.getHistory()
    });

    /**
     *  Object  JSON String  Request 
     */
    for(const attachKey in this.getSentDataInfo()) {
      // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
      // @ts-ignore
      // console.log(attachKey + ': ' + typeof this.attachData[attachKey]);
      // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
      // @ts-ignore
      if(typeof this.getSentDataInfo()[attachKey] === 'object' || Array.isArray(this.getSentDataInfo[attachKey])) {
        // console.log(attachKey + ' is must convert');
        // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
        // @ts-ignore
        this.getSentDataInfo()[attachKey] = JSON.stringify(this.getSentDataInfo()[attachKey]);
      }
    }

    await axios({
      method: "POST",
      url: URI.getAPIString(this.request, this.category),
      data: {...this.getSentDataInfo(), authentication: this.authentication},
      headers: this.header,
    }).then((res) => {
      if(res.data.apistatus == "ERROR") {
        console.log(`ERROR: ${res.data.error_message}`);
        Dialog.alert({
          type: 'is-danger',
          title: '!',
          message: res.data.error_message,
          confirmText: '',
          canCancel: false,
          onConfirm: () => { OMS.getInstance().$router.push({name: "Redirect"}) }
        })
        FancyMessage.sendToast(`[ERID:${res.data.error_code}] ${res.data.error_message}`, 'is-danger');
        console.log(`${res.data.error_code}: ${res.data.error_message}`);
        this.result = null;
        return;
      }
      FancyMessage.sendToast(`REQUESTED SUCCESSFULLY: ${res.config.url}`, 'is-success');

      this.setResult(res.data);
      this.responseInfo = {
        apistatus: res.data.apistatus,
        error_code: res.data.error_code,
        error_message: res.data.error_message,
        error_message_display: res.data.error_message_display
      }

      this.convertResult();
      // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
      // @ts-ignore
      this.onSuccess();
    }).catch((error: any) => {
      // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
      // @ts-ignore
      this.onError();

      if(!error) {
        switch (error.response.status) {
          case 500:
            // @ts-ignore
            OMS.showErrorMessage({message: ` [HTTP_RES:${error.response.status}]`})
            break;
        }
      }

    });
    this.setState(false);
    return this;
  }

  getResponseInfo() {
    return this.responseInfo;
  }

  private convertResult() {
    switch (this.category) {
      case Category.USER:
        if(Array.isArray(this.getResult()['user'])) {
          this.setResult(TypeParser.parseUserList(this.getResult()['user']));
          break;
        }
        this.setResult(TypeParser.parseUser(this.getResult()['user']));
        break;
      case Category.TEMPLATE:
        if(Array.isArray(this.getResult()['template'])) {
          this.setResult(TypeParser.parseTemplateList(this.getResult()['template']));
          break;
        }
        this.setResult(TypeParser.parseTemplate(this.getResult()['template']));
        break;
      case Category.STANDARD_ELEMENT:
        if(Array.isArray(this.getResult()['element'])) {
          this.setResult(TypeParser.parseStandardElementList(this.getResult()['element']));
          break;
        }
        this.setResult(TypeParser.parseStandardElement(this.getResult()['element']));
        break;
      case Category.CUSTOM_ELEMENT:
        if(Array.isArray(this.getResult()['element'])) {
          this.setResult(TypeParser.parseCustomElementList(this.getResult()['element']));
          break;
        }
        this.setResult(TypeParser.parseCustomElement(this.getResult()['element']));
        break;
      case Category.MEDIA:
        if(Array.isArray(this.getResult()['media'])) {
          this.setResult(TypeParser.parseMediaList(this.getResult()['media']));
          break;
        }
        this.setResult(TypeParser.parseMedia(this.getResult()['media']));
        break;
      default:
        return this.setResult(this.getResult());
    }
    console.log("::: Converted data to " + (Array.isArray(this.getResult()) ? `Array<${this.getResult()[0].constructor.name}>` : `${this.getResult().constructor.name}`));
  }

  public log(): void {
    console.log(`[CRUD URL]::::: ${this.category}${this.request}`);
    console.log(`## -> Attached Data (Stringify):`);
    console.log(this.attachData);
    console.log(`## -> All that sent:`);
    console.log(this.getSentDataInfo());
    console.log(`## <- Received Data`);
    console.log(this.getResult());
  }

}