import {OMS} from "@/OMS";
import {Category, Request} from "@/api/URI";
import {Template} from "@/template/Template";
import {CRUD} from "@/api/CRUD";
import {TypeParser} from "@/api/TypeParser";

export class TemplateStorage {

   public static async setTemplate(id: number) {
     const crud = new CRUD(Category.TEMPLATE);
     crud.setRequest(Request.GET);
     await crud.shoot({id});
     crud.getResult();
     crud.log();
     localStorage.setItem("loadedTemplate", JSON.stringify(crud.getResult()));
  }

  public static getTemplate(): Template {
    return TypeParser.parseTemplate(JSON.parse(localStorage.getItem("loadedTemplate") as string)) as Template;
  }

  public static isLoad(): boolean {
    if(this.getTemplate() == null) return false;
    return true;
  }

}