export enum MediaType {
  IMAGE = "IMAGE",
  VIDEO = "VIDEO",
  YOUTUBE = "YOUTUBE"
}

export class Media {
  id!: number;
  name!: string;
  description!: string;
  type!: MediaType;
  directLink!: string;
  createdDate!: string;

  constructor(id: number, name: string, description: string, type: MediaType, directLink: string, createdDate: string) {
    this.id = id;
    this.name = name;
    this.description = description;
    this.type = type;
    this.directLink = directLink;
    this.createdDate = createdDate;
  }
}