import {TemplateElement} from "@/template/TemplateElement";
import {AreaTag} from "@/template/area/AreaTag";
import {User} from "@/api/auth/User";

export class CustomElement extends TemplateElement {
  id!: number;
  name: string;
  description: string;
  owner: User;

  constructor(id: number, name: string, description: string, owner: User, tag: string, inner: string, props: object, parentTags: Array<AreaTag>, childTags: Array<AreaTag>) {
    super(tag, props, inner, parentTags, childTags);
    this.id = id;
    this.name = name;
    this.description = description;
    this.owner = owner;
  }

  getDisplayName() {
    return `${this.name}`;
  }

}