import Vue from 'vue'
import Vuex from 'vuex'
import createPersistedState from 'vuex-persistedstate'

import {Template} from "@/template/Template";
import {TemplateElement} from "@/template/TemplateElement";
import {StandardElement} from "@/template/StandardElement";
import {CustomElement} from "@/template/CustomElement";
import {AreaTag} from "@/template/area/AreaTag";
import {TypeTag} from "@/template/area/TypeTag";

Vue.use(Vuex)

export default new Vuex.Store({
  plugins: [createPersistedState({
    storage: window.sessionStorage,
  })],
  state: {
    debugMode: false as boolean,
    template: {} as Template,
    standardElement: [] as Array<StandardElement>,
    customElement: [] as Array<CustomElement>,

    selectMode: TypeTag.TEXT as TypeTag,
    selectedElement: {} as TemplateElement,
    hoveredStandardElement: {} as StandardElement,

    countdownTime: 7
  },
  mutations: {
    SET_DEBUG_MODE(state, mode: boolean) { state.debugMode = mode; },
    SET_TEMPLATE(state, template: Template) { state.template = template; },
    UPDATE_SELECTED_ELEMENT(state, element: TemplateElement) { state.selectedElement = element; },
    SET_STANDARD_ELEMENTS(state, elements: Array<StandardElement>) { state.standardElement = elements },
    SET_CUSTOM_ELEMENTS(state, elements: Array<CustomElement>) { state.customElement = elements },
    UPDATE_HOVERED_STANDARD_ELEMENT(state, element: StandardElement) { state.hoveredStandardElement = element },
    SET_COUNTDOWN_TIME(state, seconds: number) { state.countdownTime = seconds },

    SET_SELECT_MODE(state, select: TypeTag) { state.selectMode = select },
  },
  getters: {
    isDebugMode(state): boolean { return state.debugMode; },
    template(state): Template { return state.template; },
    selectedElement(state): TemplateElement { return state.selectedElement; },
    standardElement(state): Array<StandardElement> { return state.standardElement; },
    customElement(state): Array<CustomElement> { return state.customElement; },
    hoveredStandardElement(state): StandardElement { return state.hoveredStandardElement; },
    countdownTime(state): number { return state.countdownTime; },

    selectMode(state): TypeTag { return state.selectMode },
  },
  actions: {
    setDebugMode(context, mode: boolean) { context.commit("SET_DEBUG_MODE", mode); },
    setTemplate(context, template: Template){ context.commit("SET_TEMPLATE", template); },
    updateSelectedElement(context, element: TemplateElement) { context.commit("UPDATE_SELECTED_ELEMENT", element); },
    setStandardElement(context, elements: Array<StandardElement>) { context.commit("SET_STANDARD_ELEMENTS", elements); },
    setCustomElement(context, elements: Array<CustomElement>) { context.commit("SET_CUSTOM_ELEMENTS", elements); },
    updateHoveredStandardElement(context, element: StandardElement) {context.commit("UPDATE_HOVERED_STANDARD_ELEMENT", element)},
    setCoundownTime(context, seconds: number) {context.commit("SET_COUNTDOWN_TIME", seconds)},

    setSelectMode(context, selectMode: TypeTag) {context.commit("SET_SELECT_MODE", selectMode)},
  },
  modules: {}
})
